/* src/components/BarberPage.css */
.barber-page {
    text-align: center;
    margin-top: 50px;
}

.slot-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.slot {
    padding: 10px;
    margin: 5px;
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.slot.available {
    background-color: #00a000;
}

.slot.unavailable {
    background-color: lightgray;
    cursor: not-allowed;
}

.book-appointment {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: black;
    color: white;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
}

.book-appointment:hover {
    background-color: gray;
    transform: scale(1.1);
}
