.landing-page {
    text-align: center;
    margin-top: 50px;
}

.main-heading {
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.scissors-icon {
    font-size: 5rem;
    transition: transform 0.3s;
}

.scissors-icon.rotate {
    transform: rotate(15deg);
}

.book-button {
    padding: 10px 20px;
    background-color: black;
    color: white;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    margin-top: 20px;
}

.book-button:hover {
    background-color: gray;
    transform: scale(1.1);
}

/* Barber Cards Container */
.barber-cards-container {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
    flex-wrap: wrap;  /* Allow wrapping for smaller screens */
}

/* Barber Card */
.barber-card {
    width: 200px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
    transition: transform 0.3s ease;
    margin: 10px; /* Add spacing between cards */
}

.barber-card:hover {
    transform: scale(1.05);
}

.barber-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
}

/* Barber Card Link */
.view-details {
    display: block;
    margin-top: 10px;
    color: black;
    text-decoration: none;
}

.view-details:hover {
    text-decoration: underline;
}

/* Search Input */
.search-input {
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 300px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
}

.search-input:focus {
    border-color: black;
    outline: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    /* Adjust heading font size on small screens */
    .main-heading {
        font-size: 2rem;
    }

    /* Scissors icon adjustment */
    .scissors-icon {
        font-size: 4rem;
    }

    /* Barber cards container - stack items vertically */
    .barber-cards-container {
        flex-direction: column;
        align-items: center;
    }

    /* Barber card adjustments */
    .barber-card {
        width: 90%;  /* Make cards wider on smaller screens */
        max-width: 350px;  /* Limit the width */
        margin: 10px 0;  /* Space between cards vertically */
    }

    /* Search input adjustment */
    .search-input {
        width: 80%;  /* Make search input wider on mobile */
        max-width: 400px;
    }
}

@media (max-width: 480px) {
    /* Further adjustments for very small screens (phones in portrait) */
    .main-heading {
        font-size: 1.5rem;  /* Smaller font size */
    }

    .scissors-icon {
        font-size: 3.5rem;  /* Smaller scissors icon */
    }

    .book-button {
        font-size: 1rem;  /* Smaller button text */
        padding: 8px 16px;  /* Smaller button */
    }

    .search-input {
        width: 90%;  /* Take up more width on very small screens */
        font-size: 14px;  /* Smaller input text */
    }

    .barber-card {
        width: 95%;  /* Almost full width on small screens */
    }
}
